function sum(a, b) {
  return a + b;
}

// named exports
// pode ter vrios no mesmo arquivo
// s pode chamar com o mesmo nome
// import precisa das chaves
export function sub(a, b) {
  return a - b;
}

function mult(a, b) {
  return a * b;
}

function div(a, b) {
  return a / b;
}

export { mult as multiplicacao, div };
// mtodo principal
// s pode ter um default por arquivo
// importa com qualquer nome
// no precisa utilizar chaves
export default sum;
